var View = function(game, $el){
  this.game = game;
  this.$el = $el;
  this.setupTowers();
};

View.prototype.setupTowers = function() {
  for (var i = 0; i < 3; i++) {
    var $towers = $("<ul>").addClass("tower");
  }
  var $firstTower = $towers.eq(0);
  for (var j = 1; j < 4; j++) {
    var $disk = $("<li>").addClass("disk-" + j);
    $firstTower.append($disk);
  }
  this.$el.append($towers);
};




module.exports = View;



